import React, { ReactNode } from 'react';
import { useFocusRing } from 'react-aria';
import classNames from 'classnames';

import styles from './SkipToContent.module.scss';

export interface SkipToContentProps {
    className?: string;
    children?: ReactNode;
    targetId: string;
    ariaLabel?: string;
    gaName?: string;
}

const SkipToContent = ({
    className,
    children,
    targetId,
    ariaLabel,
}: SkipToContentProps) => {
    const { isFocusVisible, focusProps } = useFocusRing();

    const classList = classNames(className, styles['skip-to-content'], {
        [styles['focus-ring']]: isFocusVisible,
    });

    return (
        <a
            {...focusProps}
            href={targetId}
            className={classList}
            aria-label={ariaLabel}
        >
            {children}
        </a>
    );
};

export default SkipToContent;
